:root{
  --primary: #d04070;
  --secondary: #954bfc;
  --dark: #161d2a;
  --light: #eee;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

body{
  background-image: linear-gradient(95.2deg, #f2e2df 30%, #efb3b1 65%);
}

.App{
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  padding: 1px 16px 16px 16px;
}

.calculator{
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 400px;
  background-color: #ffffff;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0px 2px 64px rgba(0, 0, 0, 0.2);
}

.display{
  padding: 16px;
  min-height: 63px;
  text-align: right;
  background-color: var(--dark);
  color: var(--light);
  font-size: 24px;
  font-weight: 300;
}

button{
  appearance: none;
  border: none;
  outline: none;
  color: #ffffff;
  font-size: 20px;
  padding: 16px;
  cursor: pointer;
  transition: 0.3s;
}

button:hover{
  opacity: 0.9;
}

.operators{
  display: flex;
  flex-wrap: wrap;
}
.operators button{
  flex: 1 1 66.666%;
  max-width: 33.333%;
  background-color: var(--primary);
  font-weight: 100;
  border-style: dotted;
  border-radius: 0.5px;
  border-width: 0.1px;
}
.extra{
  display: flex;
  flex-wrap: wrap;
}
.extra button{
  flex: 1 1 50%;
  max-width: 50%;
  background-color: #778fd2;
  font-weight: 100;
  border-style: dotted;
  border-radius: 0.5px;
  border-width: 0.1px;
}

.digits{
  display: flex;
  flex-wrap: wrap;
}
.digits button{
  flex: 1 1 33.333%;
  max-width: 33.333%;
  background-color: var(--dark);
}